import { work as fetchWork } from "./api/works.js";
import { useState, useEffect } from "react";
import { Link, useParams } from "react-router-dom";
import { DefaultRating, ExperienceGrowth, ToFixed } from "./Formula";

export default function Works() {
  const [error, setError] = useState(null);
  const [work, setWork] = useState(null);
  const { id } = useParams();

  useEffect(() => {
    fetchWork(id).then(setWork).catch(setError);
  }, [id]);

  if (error) {
    return <div className="mt-4">Error: {error.message ?? error.status}</div>;
  } else if (work) {
    const rows = work.Ratings.map((rating) => (
      <tr key={rating.Id}>
        <td>
          <Link to={/authors/ + rating.AuthorId}>{rating.AuthorEmail}</Link>
        </td>
        {rating.PreviousRatingId ? (
          <td className="text-end">
            <Link to={/ratings/ + rating.PreviousRatingId}>
              {ToFixed(rating.PreviousRating)}
            </Link>
          </td>
        ) : (
          <td className="text-end">{ToFixed(1500)} (initial)</td>
        )}
        <td className="text-end">
          <Link to={/ratings/ + rating.Id}>{ToFixed(rating.Value)}</Link>
        </td>
      </tr>
    ));

    return (
      <>
        <h1 className="mt-5">Work</h1>
        <table className="table">
          <tbody>
            <tr>
              <th scope="row">Id</th>
              <td>{work.Id}</td>
            </tr>
            <tr>
              <th scope="row">Author</th>
              <td>
                <Link to={/authors/ + work.AuthorId}>{work.AuthorEmail}</Link>
              </td>
            </tr>
            <tr>
              <th scope="row">Rating</th>
              {work.UsedRatingId ? (
                <td>
                  <Link to={/ratings/ + work.UsedRatingId}>
                    {ToFixed(work.UsedRating)}
                  </Link>
                </td>
              ) : (
                <td>{ToFixed(DefaultRating)} (initial)</td>
              )}
            </tr>
            <tr>
              <th scope="row">Experience growth</th>
              <td>{ToFixed(ExperienceGrowth(work), 0)}</td>
            </tr>
            <tr>
              <th scope="row">New Rating</th>
              {work.NewRating ? (
                <td>
                  <Link to={/ratings/ + work.NewRatingId}>
                    {ToFixed(work.NewRating)}
                  </Link>
                </td>
              ) : (
                <td></td>
              )}
            </tr>
            <tr>
              <th scope="row">Pull request</th>
              {work.Link ? (
                <td>
                  <a href={work.Link}>{work.Link}</a>
                </td>
              ) : (
                <td>none</td>
              )}
            </tr>
            <tr>
              <th scope="row">Merge commit</th>
              <td>{work.MergeCommit}</td>
            </tr>
            <tr hidden={!work.SinceCommit}>
              <th scope="row">Major update</th>
              <td>{work.SinceCommit}</td>
            </tr>
            <tr>
              <th scope="row">Merged at</th>
              <td>{new Date(work.CreatedAt).toLocaleString()}</td>
            </tr>
          </tbody>
        </table>
        <h4>Deleted lines authors</h4>
        <table className="table">
          <thead>
            <tr>
              <th scope="col">Author</th>
              <th scope="col" className="text-end">
                Previous Rating
              </th>
              <th scope="col" className="text-end">
                New Rating
              </th>
            </tr>
          </thead>
          <tbody>{rows}</tbody>
        </table>
      </>
    );
  } else {
    return <div className="mt-4">Loading...</div>;
  }
}
