import { dbInit } from "./database.js";

export async function author(id) {
  const db = await dbInit();

  const s1 = db.prepare(
    "SELECT Id, Email, Organization, Repository, CreatedAt FROM Author WHERE Id = $id"
  );

  const s2 = db.prepare(
    `SELECT
      R1.Id,
      R1.Rating Value,
      R1.PreviousRatingId,
      R2.Rating PreviousRating,
      R1.WorkId,
      R1.AuthorId,
      A.Email,
      W.CreatedAt
    FROM Rating R1
    INNER JOIN Work w ON w.Id = R1.WorkId
    LEFT JOIN Rating R2 ON R1.PreviousRatingId = R2.Id
    INNER JOIN Author A on R1.AuthorId = A.Id
    WHERE R1.AuthorId = $id
    ORDER BY w.CreatedAt`
  );

  const a = s1.getAsObject({ $id: id });

  s2.bind({ $id: id });

  a.Ratings = [];

  while (s2.step()) {
    const row = s2.getAsObject();
    a.Ratings.push(row);
  }

  s2.free();
  s1.free();

  return a;
}
