export const DefaultRating = 1500;
export const n = 400;
export const ranks = 3;

export function WinProbability(a, b) {
  return Math.pow(10, a / n) / (Math.pow(10, a / n) + Math.pow(10, b / n));
}

export function RatingPercentile(rating) {
  return WinProbability(rating, DefaultRating);
}

export function RatingWithExpectedWinProb(b, probability) {
  return b - n * Math.log10(1 / probability - 1);
}

export function RatingWithExpectedWinProbAgainstDefault(probability) {
  return RatingWithExpectedWinProb(DefaultRating, probability);
}

export function RatingDelta(work) {
  return (
    (work.NewRating ?? work.UsedRating ?? DefaultRating) -
    (work.UsedRating ?? DefaultRating)
  );
}

export function MinimalPrSize(rating) {
  return (1 - WinProbability(rating, DefaultRating)) * 50;
}

export function ExperienceGrowth(work) {
  return WinProbability(work.UsedRating ?? DefaultRating, DefaultRating) * 100;
}

export function ToFixed(n, fractions = 2) {
  return n.toLocaleString(undefined, {
    minimumFractionDigits: fractions,
    maximumFractionDigits: fractions,
  });
}