import { dbInit } from "./database.js";
import { ratings as fetchRatings } from "./ratings.js";

export async function works() {
  const db = await dbInit();

  const s1 = db.prepare(
    `SELECT
      Work.Id,
      Link,
      MergeCommit,
      SinceCommit,
      Work.AuthorId,
      UsedRatingId,
      R2.Rating UsedRating,
      A.Email as AuthorEmail,
      R3.Id NewRatingId,
      R3.Rating NewRating,
      Work.CreatedAt
    FROM Work
    LEFT JOIN Rating R2 on Work.UsedRatingId = R2.Id
    INNER JOIN Author A on Work.AuthorId = A.Id
    LEFT JOIN Rating R3 on R3.WorkId = Work.Id AND R3.AuthorId = Work.AuthorId`
  );

  const s2 = db.prepare(`SELECT Id FROM Rating WHERE WorkId = $WorkId`);

  const works = [];

  while (s1.step()) {
    const row = s1.getAsObject();

    await fetchRatings(row.Id).then((ratings) => {
      row.Ratings = ratings;
    });

    works.push(row);
  }

  s2.free();
  s1.free();

  return works;
}

export async function work(id) {
  const db = await dbInit();

  const s1 = db.prepare(
    `SELECT
      Work.Id,
      Link,
      MergeCommit,
      SinceCommit,
      Work.AuthorId,
      UsedRatingId,
      R2.Rating UsedRating,
      A.Email AuthorEmail,
      R3.Id NewRatingId,
      R3.Rating NewRating,
      Work.CreatedAt
    FROM Work
    LEFT JOIN Rating R2 on Work.UsedRatingId = R2.Id
    INNER JOIN Author A on Work.AuthorId = A.Id
    LEFT JOIN Rating R3 on R3.WorkId = Work.Id AND R3.AuthorId = Work.AuthorId
    WHERE Work.Id = $id`
  );

  const w = s1.getAsObject({ $id: id });

  s1.free();

  await fetchRatings(w.Id).then((ratings) => {
    w.Ratings = ratings.filter((r) => r.AuthorId !== w.AuthorId);
  });

  return w;
}
