import { rating as fetchRating } from "./api/ratings";
import { useState, useEffect } from "react";
import { Link, useParams } from "react-router-dom";
import { DefaultRating, ToFixed } from "./Formula";

export default function Ratings() {
  const [error, setError] = useState(null);
  const [rating, setRating] = useState(null);
  const { id } = useParams();

  useEffect(() => {
    fetchRating(id).then(setRating).catch(setError);
  }, [id]);

  if (error) {
    return <div className="mt-4">Error: {error.message ?? error.status}</div>;
  } else if (rating) {
    return (
      <>
        <h1 className="mt-5">Rating update</h1>
        <table className="table">
          <tbody>
            <tr>
              <th scope="row">Id</th>
              <td>{rating.Id}</td>
            </tr>
            <tr>
              <th scope="row">Author</th>
              <td>
                <Link to={/authors/ + rating.AuthorId}>
                  {rating.AuthorEmail}
                </Link>
              </td>
            </tr>
            <tr>
              <th scope="row">Previous rating</th>
              {rating.PreviousRatingId ? (
                <td>
                  <Link to={/ratings/ + rating.PreviousRatingId}>
                    {ToFixed(rating.PreviousRating)}
                  </Link>
                </td>
              ) : (
                <td>{ToFixed(DefaultRating)} (initial)</td>
              )}
            </tr>
            <tr>
              <th scope="row">New rating</th>
              <td>{ToFixed(rating.Value)}</td>
            </tr>
            <tr>
              <th scope="row">Updated by Work</th>
              <td>
                <Link to={/works/ + rating.WorkId}>work{rating.WorkId}</Link>
              </td>
            </tr>
            <tr>
              <th scope="row">Updated at</th>
              <td>{new Date(rating.CreatedAt).toLocaleString()}</td>
            </tr>
          </tbody>
        </table>
      </>
    );
  } else {
    return <div className="mt-4">Loading...</div>;
  }
}
