import { dbInit } from "./database.js";

export async function rating(id) {
  const db = await dbInit();

  const s1 = db.prepare(
    `SELECT
      R1.Id,
      R1.Rating Value,
      R1.PreviousRatingId,
      R2.Rating PreviousRating,
      R1.WorkId,
      R1.AuthorId,
      A.Email AuthorEmail,
      W.CreatedAt
    FROM Rating R1
    INNER JOIN Work W on R1.WorkId = W.Id
    LEFT JOIN Rating R2 ON R1.PreviousRatingId = R2.Id
    INNER JOIN Author A on R1.AuthorId = A.Id
    WHERE R1.Id = $id`
  );

  const r = s1.getAsObject({ $id: id });

  s1.free();

  return r;
}

export async function ratings(workId) {
  const db = await dbInit();

  const s1 = db.prepare(
    `SELECT
      R1.Id,
      R1.Rating Value,
      R1.PreviousRatingId,
      R2.Rating PreviousRating,
      R1.WorkId,
      R1.AuthorId,
      A.Email AuthorEmail,
      W.CreatedAt
    FROM Rating R1
    INNER JOIN Work W on R1.WorkId = W.Id
    LEFT JOIN Rating R2 ON R1.PreviousRatingId = R2.Id
    INNER JOIN Author A on R1.AuthorId = A.Id
    WHERE R1.WorkId = $workId`
  );

  s1.bind({ $workId: workId });

  const ratings = [];

  while (s1.step()) {
    const row = s1.getAsObject();
    ratings.push(row);
  }

  s1.free();

  return ratings;
}
