import { author as fetchAuthor } from "./api/authors";
import { useState, useEffect } from "react";
import { Link, useParams } from "react-router-dom";
import {
  DefaultRating,
  // MinimalPrSize,
  RatingPercentile,
  RatingWithExpectedWinProbAgainstDefault,
  ranks,
  ToFixed,
} from "./Formula";

import {
  XAxis,
  YAxis,
  Tooltip,
  ResponsiveContainer,
  AreaChart,
  Area,
  ReferenceLine,
} from "recharts";

const ratingPerRank = [
  ToFixed(1000),
  ToFixed(RatingWithExpectedWinProbAgainstDefault(1 / ranks)),
  ToFixed(RatingWithExpectedWinProbAgainstDefault(2 / ranks)),
  // ToFixed(RatingWithExpectedWinProbAgainstDefault(3 / ranks)),
  // ToFixed(RatingWithExpectedWinProbAgainstDefault(4 / ranks)),
];

const texts = ["Low", "Medium", "High", "Platinum", "Diamond"];

const icons = [
  "/ranks/bronze.png",
  "/ranks/silver.png",
  "/ranks/gold.png",
  "/ranks/platinum.png",
  "/ranks/diamond.png",
];

function BadgeFunction(author) {
  const rank = Math.floor(
    RatingPercentile(author.Ratings.findLast(() => true)?.Value ?? DefaultRating) * ranks
  );

  return (
    <>
      {texts[rank]}
      <img className="ms-2" src={icons[rank]} alt={texts[rank]} width="50px" />
    </>
  );
}

const CustomTooltip = ({ active, payload, label }) => {
  if (active && payload) {
    return (
      <div>
        {new Date(label).toLocaleString()}
        <br />
        Rating: {ToFixed(payload[0].value)}
        <br />
        Work: {payload[0].payload.WorkId}
      </div>
    );
  }

  return null;
};

export default function Authors() {
  const [error, setError] = useState(null);
  const [author, setAuthor] = useState(null);
  const { id } = useParams();

  useEffect(() => {
    fetchAuthor(id).then(setAuthor).catch(setError);
  }, [id]);

  if (error) {
    return <div className="mt-4">Error: {error.message ?? error.status}</div>;
  } else if (author) {
    return (
      <>
        <h1 className="mt-5">Author</h1>
        <table className="table">
          <tbody>
            <tr>
              <th scope="row">Id</th>
              <td>{author.Id}</td>
            </tr>
            <tr>
              <th scope="row">Email</th>
              <td>{author.Email}</td>
            </tr>
            <tr>
              <th scope="row">Current rating</th>
              <td>
                {author.Ratings.findLast(() => true) ? (
                  <Link to={/ratings/ + author.Ratings.findLast(() => true).Id}>
                    {ToFixed(author.Ratings.findLast(() => true).Value)}
                  </Link>
                ) : (
                  ToFixed(1500) + " (initial)"
                )}
              </td>
            </tr>
            <tr>
              <th scope="row">Rank</th>
              <td>{BadgeFunction(author)}</td>
            </tr>
          </tbody>
        </table>
        <h1 className="mt-5">Rating history</h1>
        <ResponsiveContainer aspect={3 / 1}>
          <AreaChart
            data={author.Ratings.map((r) => ({
              CreatedAt: new Date(r.CreatedAt).getTime(),
              Value: r.Value,
              WorkId: r.WorkId,
            }))}
            margin={{ top: 0, right: 0, left: 0, bottom: 20 }}
          >
            <Tooltip
              wrapperStyle={{
                backgroundColor: "#fff",
                border: "1px solid #dcdcdc",
              }}
              content={<CustomTooltip />}
            />
            <XAxis
              dataKey="CreatedAt"
              type="number"
              name="Time"
              domain={["dataMin", "dataMax"]}
              tickFormatter={(unixTime) =>
                new Date(unixTime).toLocaleDateString()
              }
              label={{ value: "Time", offset: 0, position: "insideBottom" }}
            />
            <YAxis
              domain={[1200, 1800]}
              name="Rating"
              label={{ value: "Rating", angle: -90, position: "insideLeft" }}
            />
            <ReferenceLine
              y={ratingPerRank[0]}
              label={{ value: texts[0], position: "top" }}
            />
            <ReferenceLine
              y={ratingPerRank[1]}
              label={{
                value: texts[1] + " " + ratingPerRank[1] + "+",
                position: "top",
              }}
            />
            <ReferenceLine
              y={ratingPerRank[2]}
              label={{
                value: texts[2] + " " + ratingPerRank[2] + "+",
                position: "top",
              }}
            />
            {/* <ReferenceLine
              y={ratingPerRank[3]}
              label={{ value: "Platinum " + ratingPerRank[3] + "...", position: "top" }}
            />
            <ReferenceLine
              y={ratingPerRank[4]}
              label={{ value: "Diamond " + ratingPerRank[4] + "...", position: "top" }}
            /> */}
            <Area
              dataKey="Value"
              name="Rating"
              stroke={"rgba(30, 183, 255)"}
              fill={"rgba(30, 183, 255, 0.6)"}
            />
          </AreaChart>
        </ResponsiveContainer>
      </>
    );
  } else {
    return <div className="mt-4">Loading...</div>;
  }
}
