const ratingPerRank = [
     1000,
     RatingWithExpectedWinProbAgainstDefault(1 / ranks),
     RatingWithExpectedWinProbAgainstDefault(2 / ranks),
  // RatingWithExpectedWinProbAgainstDefault(3 / ranks),
  // RatingWithExpectedWinProbAgainstDefault(4 / ranks),
];

const texts = ["Low", "Medium", "High", "Platinum", "Diamond"];

const icons = [
  "/ranks/bronze.png",
  "/ranks/silver.png",
  "/ranks/gold.png",
  "/ranks/platinum.png",
  "/ranks/diamond.png",
];

function BadgeFunction(rating) {
  const rank = Math.floor(RatingPercentile(rating) * ranks);

  return (
    <img className="ms-2" src={icons[rank]} alt={texts[rank]} height="32px" />
  );
}

function Leaderboard(works) {
  const result = {};

  works.forEach((w) => {
    if (!result[w.AuthorEmail]) {
      result[w.AuthorEmail] = {
        Prs: 0,
        AuthorEmail: w.AuthorEmail,
        AuthorId: w.AuthorId,
        LastRating: w.NewRating ?? w.UsedRating,
        LastRatingId: w.NewRatingId ?? w.UsedRatingId,
      };
    }

    w.Ratings.forEach((r) => {
      if (!result[r.AuthorEmail]) {
        result[r.AuthorEmail] = {
          Prs: 0,
          AuthorEmail: r.AuthorEmail,
          AuthorId: r.AuthorId,
          LastRating: r.Value,
          LastRatingId: r.Id,
        };
      }
    });

    result[w.AuthorEmail].Prs++;
  });

  return Object.entries(result)
    .map((a) => a[1])
    .sort(
      (a, b) =>
        (b.LastRating ?? DefaultRating) - (a.LastRating ?? DefaultRating)
    );
}

function RatingsData(works) {
  const today = new Date();
  const worksPerDay = _.groupBy(works, (w) =>
    Math.floor((today - new Date(w.CreatedAt)) / msPerDay)
  );

  const result = Array(days)
    .fill({})
    .map((item, index) => ({
      Day: index,
    }));

  Object.entries(worksPerDay).forEach(([day, worksOfDay]) => {
    if (result[day]) {
      result[day]["Delta"] = worksOfDay
        .filter((work) => work.NewRating)
        .reduce(
          (accum, current) =>
            accum + current.NewRating - (current.UsedRating ?? DefaultRating),
          0
        );

      worksOfDay.reverse().forEach((work) => {
        result[day][work.AuthorEmail] = work.NewRating ?? result[day][work.AuthorEmail];

        work.Ratings.forEach((r) => {
          result[day][r.AuthorEmail] = r.Value ?? result[day][r.AuthorEmail];
        });
      });
    }
  });

  return result.reverse();
}

function RatingLines(works) {
  const authors = _.uniq(
    works
      .map((w) => [w.AuthorEmail, ...w.Ratings.map((r) => r.AuthorEmail)])
      .reduce(function (a, b) {
        return a.concat(b);
      })
  );

  return authors.map((a, i) => (
    <Line
      key={a}
      connectNulls
      yAxisId="left"
      type="monotone"
      strokeWidth={2}
      dataKey={a}
      stroke={colors[i % colors.length]}
    />
  ));
}

