function DaysAgoToDateString(daysAgo) {
  const today = new Date();
  today.setUTCDate(today.getUTCDate() - daysAgo);
  return today.toLocaleDateString();
}

function ToFixedTwo(n) {
  return ToFixed(n, 2);
}

